import message from '../api/message'
import * as api from '../api/api'
import utils from '../utils'

//
export const SendPoint = ({commit, state}, params) => {
//params = utils.requireData(params)
  return api.SendPoint(params).then((res) => {
    commit('SendPoint', { params })
  }).catch((err) => {
    console.log(err)
  })
}
// 2
export const GetQrCodeChk = ({commit, state}, params) => {
  params = utils.requireData(params)
  return api.GetQrCodeChk(params).then((res) => {
    res = res.data
    commit('GetQrCodeChk', { res })
    return res
  }).catch((err) => {
  	state.comm.loading = false
  })
}

