// 4.1
export const QueryRecenShifts = ({commit, state}) => {
  let params = utils.requireData()
  state.comm.loading = true
  return api.QueryRecenShifts(params).then((res) => {
    state.comm.loading = false
    res = res.data
    if(res.shiftInfo !== undefined) {
      commit('QueryRecenShifts', { res: res.shiftInfo })
    }
    return res
  }).catch((err) => {
    state.comm.loading = false
  })
}

