export default {
	GpsControl (state, { GpsControl }) {
		state.GpsControl = GpsControl,
		state.Gpss.push(GpsControl)
	},
  isLoading (state, { isLoading }) { // loading
    state.comm.loading = isLoading
  },
  updateDirection (state, { direction }) {
    state.comm.direction = direction
  },
  changeIndexConf (state, data) { //header
    Object.assign(state.comm.indexConf, data)
  },
  changeTabSelected (state, id) { // tabnav
    state.comm.tabSelected = id
  },
  pushPoints (state, point) {  // 
    state.POINTS.push(point)
    let points = JSON.stringify(state.POINTS)
    localStorage.setItem('POINTS', points)
  },
  // 
  putStatis (state, { params }) {
    state.statisParams = params
  },
  // 1 
  SendPoint (state, { params }) {
    state.POINTS.push(params)
    let points = JSON.stringify(state.POINTS)
    localStorage.setItem('POINTS', points)
  },
  
  // 2
  GetQrCodeChk (state, { res }) {
    state.responseData['GetQrCodeChk'] = res.data
  },
  
  // 3
  GetHandChk (state, { res }) {
    state.responseData['GetHandChk'] = res.data
  },
  // 3.1
  QueryTicketInfos (state, { res }) {
    state.responseData['QueryTicketInfos'] = res.ticketInfos
  },
  
  // 4
  QueryChkShifts (state, { res }) {
    state.responseData['QueryChkShifts'] = res.shiftInfos
  },
  
  // 4.1
  QueryRecenShifts (state, { res }) {
    let shiftInfos = res
//  state.responseData['QueryChkShifts'] = shiftInfos
      state.responseData['CurShiftInfo'] = shiftInfos
      state.responseData['QueryStationChk'] = {
			    chkedTicketNum: shiftInfos.chkedTicketNum,
			    totalTicketNum: shiftInfos.totalTicketNum,
			    stationName: shiftInfos.stationName,
			    stationId: shiftInfos.stationId,
			    shiftId: shiftInfos.shiftId
		  }
      let recenShifts = JSON.stringify(shiftInfos)
		  localStorage.setItem('recenShifts', recenShifts)
  },
  
  // 5
  QueryStationChk (state, { res }) {
    state.responseData['QueryStationChk'].chkedTicketNum = res.chkedTicketNum
    state.responseData['QueryStationChk'].totalTicketNum = res.totalTicketNum
  },
  
  // 6
  QueryUnchkedList (state, { res }) {
    state.responseData['QueryUnchkedList'] = res.unchkedInfos
  },
  
  // 7
  QueryShiftsStatis (state, { res }) {
    state.responseData['QueryShiftsStatis'] = res.shiftsStatis.dateShiftNums
  },
  
  // 
  GetLoginOut (state) {
  	localStorage.removeItem('driverInfo')
    localStorage.removeItem('recenShifts')
    state.responseData['DriverInfo'] = { departTime: null }
  },
  
  // 
  GetLogin (state, { res }) {
  	state.responseData['DriverInfo'] = res.accInfo
    let driverInfo = JSON.stringify(res.accInfo)
    localStorage.setItem('driverInfo', driverInfo)
    
    let recenShifts = JSON.stringify(res.curShiftInfo)
    localStorage.setItem('recenShifts', recenShifts)
  }
}



